@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
:root {
    font-size: 60%;

    --color-background: #F0F0F7;
    --color-primary-lighter: #9871F5;
    --color-primary-light: #916BEA;
    --color-primary: #7f1cef;
    --color-primary-dark: #774DD6;
    --color-primary-darker: #6842C2;
    --color-secundary: #04D361;
    --color-secundary-dark: #04BF58;
    --color-title-in-primary: #FFFFFF;
    --color-text-in-primary: #D4C2FF;
    --color-text-title: #32264D; 
    --color-text-complement: #9C98A2;
    --color-text-base: #6A6180;
    --color-line-in-white: #E6E6F0;
    --color-input-background: #F8F8FC;
    --color-button-text: #FFFFFF;
    --color-box-base: #FFFFFF;
    --color-box-footer: #FAFAFC;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

body,
input,
button,
textarea {
    font: 500 1.6rem Poppins;
    color: #6A6180;
    color: var(--color-text-base);
}

.container {
    width: 90vw;
    max-width: 700px;
} 

@media (min-width: 700px) {
    :root { 
        font-size: 62.5%;
    }
}
#page-landing {
    border: 0;
    padding: 0;
    width: 100vw;
}

.octo {
    height: 85%;
}

.content-top {
    width: 100% !important;
    display: flex;
    justify-content: space-between;
}

.content-left {
    width: 50%;
    background-color: blue;
    color: white;
    padding-left: 40px;
    padding-top: 20px;
}

.content-right {
    display: flex;
    justify-content: center;
    background-color: #f6f8fa;
}

.name {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 120px;
    margin-top: 5rem;
}

.sobrenome {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 120px;
    margin-top: -5rem;
}

.subname {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 130px;
}

.work-desk {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 30px;
    margin-left: 0.5%;
}

.redes-socias {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-top: 50%;
}

.card-redes {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 10rem;
    text-align: center;
    align-items: center;
}

.icon-redes {
    width: 45px;
    height: 45px;
}

.content-middle {
    display: flex;
    width: 50%;
    justify-content: center;
    flex-direction: column;
}

.middle-left {
    display: flex;
    justify-content: space-between;
}

.card-line-content-1 {
    width: 50%;
    height: 450px;
    padding-left: 30px;
    padding-top: 5px;
    padding-right: 60px;
    background-color: blue;
    color: white;
}

.card-line-content {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.project-img {
    width: 15rem;
}

.title-middle {
    color: #FFA011;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 7rem;
}

.middle-right {
    display: flex;
    width: 50%;
    justify-content: center;
}

.mainMiddle {
    width: 100%;
    display: flex;
}

.dale {
    flex-direction: row-reverse;
}

.middle-right {
    display: flex;
    flex-direction: column;
    padding-left: 2rem;
}

.right-middle-text {
    color: #FFA011;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 7rem;
}

.right-desc-text {
    color: blue;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 2.5rem;
}

.umf {
    width: 35rem;
}

.tomorrow {
    margin-top: -60rem;
}

.teste {
    text-align: center;
    font-size: 35rem;
    background-color: blue;
    margin-top: 25%;
}
#page-teacher-list {
    width: 100vw;
    height: 100vh;
}

#search-teachers {
    margin-top: 3.2rem;
}

#search-teachers label {
    color: var(--color-text-in-primary);
}

#search-teachers .input-block {
    position: relative;
}

#search-teachers .input-block + .input-block{
    position: relative;
    margin-top: 1.4rem;
}

#search-teachers .input-block label {
    font-size: 1.4rem;
}

#search-teachers .input-block input {
    width: 100%;
    height: 5.6rem;
    margin-top: 0.8rem;
    border-radius: 0.8rem;
    background-color: var(--color-input-background);
    border: 1px solid var(--color-line-in-white);
    outline: 0;
    padding: 0 1.6rem;
    font: 1.6rem Archivo;
}

#search-teachers .input-block:focus-within::after {
    width: calc(100% - 3.2rem);
    height: 2px;
    content: '';
    background-color: var(--color-primary-light);
    position: absolute;
    left: 1.6rem;
    right: 1.6rem;
    bottom: 0;
}

#page-teacher-list main {
    margin: 3.2rem auto;
    width: 90%;
}


@media (min-width: 700px) {
    #page-teacher-list {
        max-width: 100%;
    }

    #search-teachers {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 16px;
        -webkit-column-gap: 16px;
                column-gap: 16px;
        position: absolute;
        bottom: -28px;   
    }

    
    #search-teachers .input-block + .input-block{
        position: relative;
        margin-top: 0;
    }

    #page-teacher-list main {
        padding: 3.2rem 0;
        max-width: 740px;
        margin: 0 auto;
    }

}
.page-header {
    display: flex;
    flex-direction: column;
}

.page-header .top-bar-container {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--color-primary);
    padding: 1.6rem 0;
}

.page-header .top-bar-container > a{ 
    height: 3.2rem;
    transition: opacity 0.2s; 
}

.page-header .top-bar-container > a:hover{ 
    opacity: 0.6s; 
}

.page-header .top-bar-container > img{ 
    height: 1.6rem;
}

.page-header .header-content {
    width: 90%;
    margin: 0 auto;
    position: relative;
    margin: 3.2rem auto;
}

.page-header .header-content strong{
    font: 700 3.6rem Archivo;
    line-height: 4.2rem;
    color: var(--color-title-in-primary);
}

.page-header .logo-houpa {
    width: 15rem;
    height: 7rem;
    flex-direction: column;
}

.page-header .identidade-houpa {
    flex-direction: row;
    display: flex;
}

.page-header .desc-tokens {
    padding-top: 15px;
}

a {
    text-decoration: none;
    color: var(--color-primary);
}

@media (min-width: 700px) {
    .page-header {
        height: 140px;
    }

   

    .page-header .header-content {
        flex: 1 1;
        max-width: 740px;
        margin: 0 auto;
        padding-bottom: 48px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

    .page-header .header-content strong {
        max-width: 350px;
    }
}
.teacher-item {
    background-color: var(--color-box-base);
    border: 1px solid var(--color-line-in-white);
    border-radius: 0.8rem;
    margin-top: 2.4rem;
    overflow: hidden;
}

.teacher-item header {
    padding: 3.2rem 2rem;
    display: flex;
    align-items: center; 
    color: white;
}

.teacher-item img {
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
}

.teacher-item header div {
    margin-left: 2.4rem;
}
 
.teacher-item header div strong{
    font: 700 2.4rem Archivo; 
    display: block; 
    color: var(--color-text-title); 
}

.teacher-item header div span{
    font-size: 1.6rem;
    display: block;
    margin-top: 0.4rem;
}

.teacher-item > p {
    padding: 0 2rem;
    font-size: 1.6rem;
    line-height: 2.8rem;
}

.teacher-item footer {
    padding: 3.2rem 2rem;
    background: var(--color-box-footer);
    border-top: 1px solid var(--color-line-in-white);
    margin-top: 3.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.teacher-item footer p strong{
    color: var(--color-primary);
    font-size: 1.6rem;
    display: block;
}

.teacher-item footer button{
    width: 20rem;
    height: 5.6rem;
    background-color: var(--color-secundary);
    color: var(--color-button-text);
    border: 0;
    border-radius: 0.8rem;
    cursor: pointer;
    font: 700 1.4rem Archivo;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    text-decoration: none;
    transition: 0.2s;
}

.teacher-item footer button:hover{
    background-color: var(--color-secundary-dark);
}

.teacher-item footer button > img{
    width: 2.8rem;
}

@media (min-width: 700px) {
    .teacher-item header,
    .teacher-item footer{
        padding: 3.2rem;
    }

    .teacher-item > p{
        padding: 0 3.2rem;
    }

    .tacher-item footer p strong {
        display: inline;
        display: initial;
        margin-left: 1.6rem;
    }

    
    .tacher-item footer button {
        width: 24.5rem;
        font-size: 1.6rem;
        justify-content: center;
    }

    .teacher-item footer button img {
        margin-right: 1.6rem;
    }
}
